
.table-responsive {
  overflow: hidden;
  overflow-x: auto;
}
.selected-filter h3 {
  font-size: 16px;
}
.selected-filter small {
  color: inherit;
  display: block;
  font-size: 13px;
  line-height: 1.3;
}
p {
  font-size: 14px;
  line-height: 1.3;
}
table input,
table select {
  margin-bottom: 0px !important;
}
table tr:nth-child(even) {
  background: none;
}
.fc .fc-toolbar button {
  border: none;
  background: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  margin-top: 3px;
}
.fc .fc-toolbar button:hover, .fc .fc-toolbar button:focus, .fc .fc-toolbar button:active {
  color: initial;
}
#calendar {
  color: #444;
  background-color: #fff;
}
#calendar table {
    margin: auto;
}
